import React from 'react';
import { makeStyles } from '@material-ui/core';

import TikTokTable from '../../components/TikTokTable';
import { TIKTOK_COLUMNS, TIKTOK_GROUP_COLUMNS } from './constants'
import ApiClient from '../../services/apiClient';
import { groupsByAdvertiser, todayStats } from '../../constants/endpoints';

const useStyles = makeStyles((theme) => ({
  root: {
  },
}));


const Dashboard = () => {
  const classes = useStyles();
  const [statistics, setStatistics] = React.useState([]);
  const [groups, setGroups] = React.useState<any>({});
  const [isLoading, setLoading] = React.useState(false);

  React.useEffect(() => {
    const loadAdvertisers = async () => {
      setLoading(true);
      try {
        const stats = await ApiClient.GET(todayStats);
        setStatistics(stats
          .map(({ metrics, ...item }: MetricsItem) => ({ ...metrics, ...item }))
          .sort((a: any, b: any) => Number(b.impressions) - Number(a.impressions)));
        setLoading(false);
      } catch (error) {
        setLoading(false);
        console.error(error);
      }
    }
    loadAdvertisers();
    const interval = setInterval(loadAdvertisers, 60 * 1000)
    return () => clearInterval(interval);
  }, [])

  const handleRowClick = (advertiserId: string) => {
    const getAdvertiserGroups = async (id: string) => {
      try {
        const advertiserStats = await ApiClient.GET(groupsByAdvertiser(id));
        const groupObjs = advertiserStats
          .filter(({ impressions }: any) => !!Number(impressions))
          .sort((a: any, b: any) => Number(b.impressions) - Number(a.impressions));
        setGroups({ ...groups, [advertiserId]: groupObjs })
      } catch (error) {
        console.error(error);
      }
    }
    getAdvertiserGroups(advertiserId);
  }

  return (
    <div className={classes.root}>
      <TikTokTable
        rows={statistics}
        columns={TIKTOK_COLUMNS}
        groupColumns={TIKTOK_GROUP_COLUMNS}
        isLoading={isLoading}
        onRowClick={handleRowClick}
        extendedItems={groups}
      />
    </div>
  )
}

export default Dashboard;

type TiktokMetrics = 'spend' | 'impressions' | 'cpc' | 'ctr' | 'clicks' | 'conversion' | 'cost_per_conversion' | 'conversion_rate';
interface MetricsItem {
  id: string;
  name?: string;
  metrics: { [key in TiktokMetrics]: string };
}