import React from 'react';
import { makeStyles, TableBody, TableCell, TableHead, Table } from '@material-ui/core';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import CircularProgress from '@material-ui/core/CircularProgress';

const TikTokTable: React.FC<Props> = ({ rows, columns, isLoading, onRowClick, extendedItems, groupColumns }) => {
  const classes = useStyles();

  const [openRows, setOpenRows] = React.useState<string[]>([])

  const handleRowClick = (id: string) => {
    if (id === 'overall') return;
    if (openRows.indexOf(id) === -1) {
      setOpenRows([...openRows, id]);
      onRowClick(id);
      return;
    }
    setOpenRows(openRows.filter(item => item !== id))
  }

  return (
    <div className={classes.table}>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  style={{ width: column.width }}
                  key={column.key}
                  component='th'
                >
                  {column.name}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {isLoading && (
              <TableRow>
                <TableCell component='td' colSpan={columns.length}>
                  <CircularProgress />
                </TableCell>
              </TableRow>
            )}
            {rows.map((row: any) => {
              return (
                <React.Fragment key={row.id}>
                  <TableRow className={classes.row} onClick={() => !!Number(row.impressions) && handleRowClick(row.id)}>
                    {columns.map((column) => (
                      <TableCell className={column.bold ? classes.bold : undefined} key={column.key} component='td'>{row[column.key]}</TableCell>
                    ))}
                  </TableRow>
                  {openRows.indexOf(row.id) !== -1
                    && (extendedItems[row.id]
                      ? extendedItems[row.id].map((extended: any) => (
                        <TableRow style={{ backgroundColor: '#fff'}}>
                          {groupColumns.map((column) => (
                            <TableCell key={column.key} component='td'>{extended[column.key]}</TableCell>
                          ))}
                        </TableRow>
                      )) : (
                        <TableRow style={{ backgroundColor: '#fff'}}>
                          <TableCell component='td' colSpan={groupColumns.length}>
                            <CircularProgress />
                          </TableCell>
                        </TableRow>
                      ))
                  }
                </React.Fragment>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  table: {},
  bold: {
    fontWeight: 'bold',
  },
  row: {
    '&:hover': {
      backgroundColor: '#cccaca',
    },
    backgroundColor: '#ececec',
    cursor: 'pointer',
  }
}))

interface Props {
  rows: any[];
  columns: any[];
  groupColumns: any[];
  extendedItems: any;
  isLoading: boolean;
  onRowClick: (id: string) => void;
}

export default TikTokTable;